:root {
  --primary: white;
  --secondary: black;
}

body {
  color: var(--primary);
  background: var(--secondary);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page-content {
  margin: auto;
  max-width: 700px;
  padding-top: 64px;
  padding-bottom: 64px;
  padding-left: 20px;
  padding-right: 20px;
}

.button {
  text-decoration: none;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: gold;
  cursor: pointer;
  font-size: 16px;
  border-radius: 8px;
  padding: 10px 20px;
}

h1 {
  text-align: center;
}

h3,
h4 {
  margin-bottom: 0;
}

nav {
  text-align: center;
  position: fixed;
  width: 100%;
  background: black;
  white-space: nowrap;
  overflow: auto;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 16px 16px;
}

nav a:hover {
  background: white;
  color: black;
}

.theme-icon {
  height: 25px;
  vertical-align: middle;
  padding-left: 15px;
  padding-right: 15px;
}

.arrow-icon {
  // Arrow is visible only on small screens
  @media screen and (min-width: 560px) {
    display: none;
  }

  position: fixed;
  padding-top: 12px;
  right: 0;
  width: 25px;
}

.list-item {
  text-decoration: none;
  color: var(--primary);
  padding: 16px 0 0 0;
}

.list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid var(--primary);
}

.image-20,
.image-40,
.image-60,
.image-80,
.image-100 {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.image-20 {
  width: 20%;
}

.image-40 {
  width: 40%;
}

.image-60 {
  width: 60%;
}

.image-80 {
  width: 80%;
}

.image-100 {
  width: 100%;
}
